.seatRow {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 30px;
    margin-bottom: 10px;
}

.disabled {
    border: 1px solid #666666 !important;
    background-color: #666666 !important;
    color: #666666 !important;
}


.rowTitle {
    width: 20px;
}

.seat {
    width: 30px;
    height: 30px;
    margin-left: 10px;
    text-align: center;
    border-radius: 10px;
}

.standard.no-tick {
    color: white;
}

.standard {
    border: 1px solid white;
}

.standard.tick {
    color: black;
}

.vip {
    border: 1px solid lightgreen;
    color: lightgreen;
}

.deluxe {
    border: 1px solid blue;
    color: blue;
}

.tick {
    background-color: white;
}

.no-tick {
    background-color: transparent;
}
