.priceInfoWrapper {
    background-color: white;
}

.priceInfo {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.text {
    color: black;
}

.buttonWrapper {
    display: flex;
    justify-content: center;
}

.button {
    width: 150px;
    height: 30px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 15px;
}

.flex {
    flex: 1;
    align-items: center;
}

.right {
    float: right;
}

.priceSuffix {
    font-size: 14px;
}
